import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			newsList:[],
			collectList:[]
		}
	},
	methods: {
		collect(e){
			this.newsList[e.currentTarget.dataset.id].collect=!this.newsList[e.currentTarget.dataset.id].collect
			uni.setStorage({
				key:'collectList',
				method:'GET',
				data:JSON.stringify(this.newsList)
			})
		},
		goDetail(e){
			// console.log(this.newsList[e.currentTarget.dataset.index])
			wx.navigateTo({
				url:"../details/details?item="+JSON.stringify(this.newsList[e.currentTarget.dataset.index])
			})
		}
	},
	onLoad(){
		var that=this
		uni.request({
			url:baseUrl+'index/newGoods',
			success(res){
				console.log(res.data.data.data.goods)
				for(var i=0;i<res.data.data.data.goods.length;i++){
					res.data.data.data.goods[i].collect=false
				}
				that.newsList=res.data.data.data.goods
				uni.setStorage({
					key:'collectList',
					data:JSON.stringify(that.newsList)
				})
			}
		})
	},
	onShow(){
		var that=this
		uni.getStorage({
			key:'collectList',
			success(e){
				that.newsList=JSON.parse(e.data)
				console.log(that.newsList)
			}
		})
	}
}
